body {
    font-family: "Segoe UI", sans-serif;
    background: #f7f9fc;
    margin: 0;
    padding: 0;
}

.app {
    max-width: 1200px;
    margin: auto;
    padding: 20px;
}

header {
    text-align: center;
    background-color: #2c3e50;
    color: white;
    padding: 15px;
    border-radius: 10px;
}

.content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
    margin-top: 20px;
}

.card {
    background: white;
    padding: 20px;
    border-radius: 12px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.form input {
    width: 100%;
    margin: 5px 0;
    padding: 10px;
    border-radius: 6px;
    border: 1px solid #ccc;
}

.form button {
    background-color: #2980b9;
    color: white;
    border: none;
    padding: 10px;
    width: 100%;
    margin-top: 10px;
    border-radius: 6px;
    cursor: pointer;
}

.form button:hover {
    background-color: #1c5980;
}

.apartment-list {
    list-style: none;
    padding: 0;
}

.apartment-item {
    border-bottom: 1px solid #ddd;
    padding: 10px 0;
    display: flex;
    justify-content: space-between;
}

.apartment-item div p {
    margin: 5px 0;
}

.actions button {
    margin-right: 8px;
    border: none;
    padding: 6px 10px;
    border-radius: 5px;
    cursor: pointer;
}

.actions .delete {
    background: #e74c3c;
    color: white;
}

.actions button:hover {
    opacity: 0.85;
}

.edit-btn,
.save-btn {
    margin-left: 10px;
    background: #27ae60;
    color: white;
    border: none;
    padding: 3px 8px;
    border-radius: 4px;
    cursor: pointer;
}

.edit-btn:hover,
.save-btn:hover {
    opacity: 0.85;
}

.empty {
    text-align: center;
    color: #666;
    margin-top: 20px;
}

.rent-card {
    grid-column: span 2;
}